.progress-bar,
.progress-bar1,
.progress-bar2,
.progress-bar3,
.progress-bar4,
.progress-bar5 {
    border-radius: 0 4px 4px 0; }


.progress-bar {
    background: linear-gradient(-45deg, #6adc81, #6c2ab9, #23d5ab);
    background-size: 400% 400%;
    animation: gradient 8s ease infinite; }

@keyframes gradient {
    0% {
        background-position: 0% 50%; }

    50% {
        background-position: 100% 50%; }

    100% {
        background-position: 0% 50%; } }

